import React, { Component } from 'react';
import { withTranslation } from "react-i18next";
import Top10 from "../../../Component/Top10/api";

export class apiSection extends Component {

  data = {
    Home: {
      Title: "Top Performing Funds",
      ProductCategoryId: "1",
      SebiCategoryId: "",
      SebiSubCategoryId: "",
      DefaultProductId: "1",
    },
  };

  render() {
    const { t } = this.props;
    const productinfo = this.data.Home;

    return (
        <div className="API-Section">
        <div className="container container-wrapper">
          <h2 className="text-center">{t("Home:hOME39")}</h2>
          <div className="row row justify-content-lg-center">
            <div className="col col-lg-10">
              <Top10
                productCategoryId={productinfo.ProductCategoryId}
                sebiCategoryId={productinfo.SebiCategoryId}
                sebiSubCategoryId={productinfo.SebiSubCategoryId}
                defaultProductId={productinfo.DefaultProductId}
              />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslation()(apiSection);