import React, { Component } from 'react'
import './api.css'
import { Link } from 'react-router-dom'


export class api extends Component {

    constructor() {
        super()

        this.state = {
            items: [],
            MainTitle: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        const url = 'https://api.nivesh.com/API/getSchemesDataV2';
        const postBody = {
            ProductCategoryId: this.props.productCategoryId,
            ClientCode: "",
            LanguageId: 1,
            device: "",
            AMCCode: "",
            SebiCategoryId: this.props.sebiCategoryId ? this.props.sebiCategoryId : "",
            SebiSubCategoryId: this.props.sebiSubCategoryId ? this.props.sebiSubCategoryId : "",
            DefaultProductId: this.props.defaultProductId ? this.props.defaultProductId : ""
        };
        const requestMetadata = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(postBody)
        };

        fetch(url, requestMetadata)
            .then(response => response.json())
            .then(data => {
                this.setState({ isLoaded: true, items: data.ObjectResponse.SchemeDataList, MainTitle: data.ObjectResponse.TitleResponse });
            }).catch(error=>{
                console.log(error);
            });
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.productCategoryId !== this.props.productCategoryId){
            return true;
        } else if( nextProps.sebiCategoryId !== this.props.sebiCategoryId){
            return true;
        }else if(nextProps.sebiSubCategoryId !== this.props.sebiSubCategoryId){
            return true;
        }else if(nextProps.defaultProductId !== this.props.defaultProductId){
            return true;
        }
        else if(nextState.MainTitle?.Title !== this.state.MainTitle?.Title){
            return true;
        }
        else if(JSON.stringify(nextState.items)!==JSON.stringify(this.state.items)){
            return true;
        }

        return false;
      }

    componentDidUpdate() {
        const url = 'https://api.nivesh.com/API/getSchemesDataV2';
        const postBody = {
            ProductCategoryId: this.props.productCategoryId,
            ClientCode: "",
            LanguageId: 1,
            device: "",
            AMCCode: "",
            SebiCategoryId: this.props.sebiCategoryId ? this.props.sebiCategoryId : "",
            SebiSubCategoryId: this.props.sebiSubCategoryId ? this.props.sebiSubCategoryId : "",
            DefaultProductId: this.props.defaultProductId ? this.props.defaultProductId : "",
        };
        const requestMetadata = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(postBody)
        };

        this.setState({isLoaded:false});
        fetch(url, requestMetadata)
            .then(response => response.json())
            .then(data => {
                this.setState({ isLoaded: true, items: data.ObjectResponse.SchemeDataList, MainTitle: data.ObjectResponse.TitleResponse });
            }).catch(error=>{
                console.log(error);
                this.setState({items:null});
            });
    }


    render() {
        const { isLoaded, items, MainTitle } = this.state;

        if (!isLoaded) return <div><h1> Fund Tables are Loading </h1></div>;
        return (
            <div className="api">
                <h2>{MainTitle && MainTitle[1]?.Title}</h2>

                {items && items.map((item) => (

                    <div key={item.SchemeCode}>
                        <div className='table-responsive'>
                        <table className="table api-table">
                            <thead className="thead-blue">
                                <tr>
                                    <th colSpan="4">{item.SchemeName}
                                        <a href={`https://app.nivesh.com/Home/Index?SchemeCode=${item.SchemeCode}&selectedcategoryId=${this.props.productCategoryId}`} target="_blank" className="api-button">Invest Now</a></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr id="below-head">
                                    <th>1 Year</th>
                                    <td>2 Year</td>
                                    <td>3 Year</td>
                                    <td>5 Year</td>
                                </tr>
                                <tr id="below-head-values">
                                    <th>{item.OneYearReturn}%</th>
                                    <td>{item.TwoYearReturn}%</td>
                                    <td>{item.ThreeYearReturn}%</td>
                                    <td>{item.FiveYearReturn}%</td>
                                </tr>
                            </tbody>
                        </table>
                        </div>
                    </div>
                ))}
            </div>
        )
    }
}

export default api
